* {
  box-sizing: border-box;
  font-family: 'Pontano Sans', sans-serif;
  margin: 0;
  padding: 0;
  letter-spacing: .12rem;
}

html {
  font-size: 62.5%;
  /* scroll-behavior: smooth; */
}

html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
}

#root{
  display: flex;
  flex-direction: column;
}

main {
  padding-top: 7rem;
  flex: 1 0 auto;
  background: 
    -webkit-linear-gradient(rgba(0, 0, 0, 0.6), rgba(0,0,0,0.4)),
    url(/static/media/gym-mobile.f53ff3c3.jpg)
    center / cover
    no-repeat
    fixed;
