}

a{
  text-decoration: none;
}

/* 
font-family: 'Bevan', cursive;
font-family: 'Pontano Sans', sans-serif;
*/

h1, h2, h3, h4 {
  font-family: 'Bevan', cursive;
}

p {
  font-family: 'Pontano Sans', sans-serif;
}

button{
  cursor: pointer;
}

button:disabled, button[disabled]{
  background-color: rgb(131, 131, 131) !important;
  color:rgb(170, 170, 170) !important;
  cursor: not-allowed;
}

ul {
